* {
  padding: 0;
  margin: 0;
  scroll-behavior: smooth;
}

body {
  /* background: var(--color-bg); */
  /* background: black; */
}

a {
  color: unset;
  text-decoration: none;
}



.circle_top_login {
  position: absolute;
  width: 76vmin;
  height: 76vmin;
  border-radius: 100%;
  background: rgba(49, 177, 39, 0.63);
  z-index: -1;
  left: -22vh;

  animation: roll-in-down 1500ms ease-out;
  top: -29vmin;


}

.circle_bottom_signup {
  position: absolute;
  width: 76vmin;
  height: 76vmin;
  border-radius: 100%;
  background: #31B127A1;
  z-index: -1;
  left: -22vh;

  animation: roll-in-top 1500ms ease-out;
  bottom: -29vmin;

}

.circle_bottom_login {
  position: absolute;
  bottom: -29vmin;
  right: -22vh;
  z-index: -1;
  bottom: -29vmin;
  width: 76vmin;
  height: 76vmin;
  border-radius: 100%;
  background: #24A2E9;
  animation: roll-in-top 1500ms ease-out;
}

.circle_top_signup {
  z-index: -1;
  position: absolute;
  width: 82vh;
  width: 76vmin;
  height: 76vmin;
  top: -29vmin;
  right: -22vh;
  border-radius: 100%;
  background: #24A2E9;
  animation: roll-in-down 1500ms ease-out;
}

@keyframes roll-in-top {

  0% {
    transform: translateY(-472px) rotate(-540deg);
    opacity: 1;
  }

  100% {

    transform: translateY(0px) rotate(0deg);
    opacity: 1;
  }
}

@keyframes roll-in-down {

  0% {
    transform: translateY(472px) rotate(540deg);
    opacity: 1;
  }

  100% {

    transform: translateY(0px) rotate(0deg);
    opacity: 1;
  }
}

.login_signup_container {
  z-index: -1;
  width: 100vw;
  height: 100vh;

  position: absolute;
  overflow-y: hidden;
  overflow-x: hidden;
}

.login_signup_glass {
  position: absolute;
  left: 4.9vmin;
  top: 5.7vmin;
  bottom: 5.7vmin;
  right: 4.9vmin;
  background: rgba(236, 236, 236, 0.34);
  backdrop-filter: blur(0.8vmin);
  border-radius: 5vmin;
  border: 0.3vmin solid;

  border: 1px solid;
  border-top-color: #EDEDED;
  border-right-color: #EDEDED;
  border-left-color: #E0E0E0;
  border-bottom-color: #E0E0E0;
}

.login_line {
  z-index: -1;
  position: absolute;
  top: 28vmin;
  animation: login_line 1500ms ease-out;
}

.signup_line {
  position: absolute;
  top: 37vmin;
  left: 175vmin;
  animation: signup_line 1500ms ease-out;
}

@keyframes signup_line {

  0% {
    transform: translateX(100px) rotate(200deg);
    opacity: 0;
  }

  100% {

    transform: rotate(0deg);
    opacity: 1;
  }
}

@keyframes login_line {

  0% {
    transform: translateX(100px) rotate(200deg);
    opacity: 0;
  }

  100% {

    transform: rotate(0deg);
    opacity: 1;
  }
}

.Home_curve_line {
  position: absolute;
  top: 13.5vmin;
}