.signup_canidate_btn {
    border-radius: 3vmin 0vmin 0vmin 3vmin;
    border-style: solid;
    border-width: 0.5vmin 0 0.5vmin 0.5vmin ;
    border-color: #B8DEF3;
    cursor: pointer;
    text-transform: capitalize;
    font-family: var(--font-family-Urbanist);
    font-size: 3vmin;
    color: var(--color-text);
    padding: 1.5vmin;
    width: 26vmin;
}

.signup_company_btn {
    border-radius: 0vmin 3vmin 3vmin 0vmin;
    border-style: solid;
    border-width: 0.5vmin 0.5vmin 0.5vmin 0;
    border-color: #B8DEF3;
    cursor: pointer;
    text-transform: capitalize;
    font-family: var(--font-family-Urbanist);
    font-size: 3vmin;
    color: var(--color-text);
    padding: 1.5vmin;
    width: 26vmin;
}

.btn_active {
    background: linear-gradient(89.95deg, rgba(50, 177, 38, 0.3) 1.99%, rgba(36, 162, 233, 0.3) 85.34%, rgba(36, 162, 233, 0.3) 104.58%);
}