.Navbar {
    width: 100vw;
    float: left;
    margin-top: 9vmin;
}
.Navbar_glass{
  z-index: -1;  
  padding: 4vmin;
  position: absolute;
  top: 11.3vmin;
  left: 4.8vmin;
  right: 4.8vmin;
  background: rgba(236, 236, 236, 0.34);
  backdrop-filter: blur(0.8vmin);
  border-radius: 5vmin;
  border: 0.3vmin solid;

  border: 1px solid;
  border-top-color: #EDEDED;
  border-right-color: #EDEDED;
  border-left-color: #E0E0E0;
  border-bottom-color: #E0E0E0;
}

.navbar_sign {
    position:absolute;
    left: 86%;
    align-items: center;
    width: 15vmin;
    height: 6vmin;
    border: 0.5vmin solid;
    border-radius: 2vmin;
    border-top-color: #32B126;
    border-right-color: #32B126;
    border-bottom-color: #24A2E9;
    border-left-color: #24A2E9;
    font-family: var(--font-family-Comfortaa);
    font-size: 2.6vmin;
    color: var(--color-text);
    background-color: Transparent;
    text-transform: uppercase;
}

.navbar_sign:hover {
    background: linear-gradient(89.95deg, rgba(50, 177, 38, 0.6) 1.99%, rgba(36, 162, 233, 0.6) 85.34%, rgba(36, 162, 233, 0.6) 104.58%);
}

.navbar_home_btn {
    display: grid;
    grid-column: 10/10;
    align-items: center;
    width: 18vmin;
    height: 7vmin;
    border: 0.5vmin solid;
    border-radius: 2vmin;
    border: 0.56vmin solid;
    border-top-color: #24A2E9;
    border-right-color: #24A2E9;
    border-bottom-color: #32B126;
    border-left-color: #32B126;
    font-family: var(--font-family-Comfortaa);
    font-size: 2.5vmin;
    padding-top: 1vmin;
    padding-bottom: 1vmin;
    background-color: Transparent;
    color: #fff;
}
.Font {
    font-family: var(--font-family-Comfortaa);
    font-size: 2.6vmin;
}
a:hover{
    color: linear-gradient(89.95deg, rgba(50, 177, 38, 0.6) 1.99%, rgba(36, 162, 233, 0.6) 85.34%, rgba(36, 162, 233, 0.6) 104.58%);
}










































